    // toggle class
    /*
    <div id="toggle-me">Lorem ipsum dolor sit amet, consectetuer adipiscing elit. </div>
    <a href="#" data-toggle="class" data-target="#toggle-me" data-classes="cj-bg-dark cj-fs-24" class="cj-button cj-is-success">Toggle</a>
    */
    function dataToggleClass($el) {
      var $target = $($el.data('target'));
      var classes = $el.data('classes');
      var group = $el.data('toggle-group');
      var sibling_class = $el.data('sibling-class');
      var el_self_class = $el.data('self-class');
      $(this).siblings().removeClass(sibling_class);
      $(this).addClass(sibling_class);
      if (group !== undefined) {
        $(group).addClass(classes);
      }
      if (el_self_class !== undefined) {
        $el.toggleClass(el_self_class);
      }
      $target.toggleClass(classes);

      // delayed classes if any
      var delayed_classes = $(this).data('delayed-classes');
      if (delayed_classes !== undefined) {
        delayed_classes = delayed_classes.split(',');

        var _loop = function _loop(i) {
          var timeout_class = delayed_classes[i].split('|');
          var time = timeout_class[0];
          var time_class = timeout_class[1];
          setTimeout(function () {
            $target.addClass(time_class);
          }, time);
        };

        for (var i = 0; i < delayed_classes.length; i++) {
          _loop(i);
        }
      }
    }

